.buttons__button {
    background: transparent;
    color: teal;
    border: none;
    margin: 0 0 0 20px;
    transition: color 0.3s;
}

.buttons__button:disabled {
    color: #ccc;
}

.buttons {
    display: flex;
    flex-wrap: wrap;
}
@media (max-width: 767px) {
    .buttons__button {
      flex-basis: 100%;
      margin: 10px;
    }
  }